@tailwind base;
@tailwind components;
@tailwind utilities;
@layer components {
  .imglogo{
    @apply h-12 px-1
  }
  .white{
    @apply text-[#ffffff] hover:drop-shadow-[0_0_8px_#ffffff]
  }
  .link{
    @apply text-white font-medium transition-all duration-500 opacity-80 hover:opacity-100 hover:text-white
  }
  .pokemonCard{
    @apply relative w-80  bg-[rgba(255,255,255,0.7)] text-center rounded-xl cursor-pointer border-[6px] border-[#F6BE3E]  transition-all duration-300 drop-shadow-[-2px_2px_10px_#c4c4c4] hover:-translate-y-[5px] hover:border-[#F7C940]
  }
  .imgPokemon{
    @apply opacity-90 h-[16rem] rounded-t-md rounded-br-md border-double border-4 border-white p-3 transition-all duration-300 group-hover:opacity-100
  }
  .title{
    @apply relative left-0 bg-[#F6BE3E] w-fit text-left px-3 text-[18px] font-semibold transition-all duration-300 group-hover:bg-[#F7C940] rounded-br-xl
  }
  .bd{
    @apply relative rounded-br-md rounded-tr-sm mt-4 p-4 opacity-90 group-hover:opacity-100
  }
  .bdTitle{
    @apply relative font-bold capitalize text-[16px] text-[#ffffff] text-center z-10 -skew-x-[16deg] py-1 px-2 rounded-md ml-2
  }
  .bdAbilities{
    @apply relative  font-bold capitalize text-[16px] leading-[2] text-[#ffffff] text-center  z-10 before:w-20 before:h-8 before:absolute before:-z-10 before:top-[50%] before:left-[50%] before:bg-[#000000] before:-translate-x-[50%] before:-translate-y-[50%] before:-skew-x-[16deg] before:rounded-md
  }
  .cirlcle{
    @apply absolute w-[50px] h-[50px] rounded-full opacity-100 -left-[15px] -top-[15px] 
  }
  .center{
    @apply absolute top-[50%] left-[50%] -translate-y-[50%] -translate-x-[50%]
  }
  .ability{
    @apply list-none text-white capitalize my-2 leading-[1.3] px-2 rounded-md font-medium
  }
  .cardFull{
    @apply relative flex gap-4 mt-[2.3rem] items-center w-fit h-fit  py-3 px-10 rounded-xl flex-wrap justify-center
  }
  .title2{
    @apply bg-red-500 w-fit p-2 mb-2 rounded-md font-bold text-white -skew-x-[16deg]
  }
  .id{
    @apply absolute top-2 left-2 w-fit h-fit  px-3 py-2 rounded-full font-bold bg-white border-2 border-cyan-400 
  }
  .name{
    @apply absolute left-1/2 capitalize -translate-x-1/2 my-3 py-1 px-2   bg-teal-600 text-white rounded-xl 
  }
  .stat{
    @apply list-none capitalize font-medium text-black bg-amber-400 w-fit p-1 rounded-md my-1 whitespace-nowrap
  }
  .TitleHome{
    @apply leading-[2] font-bold text-xl sm:text-2xl
  }
  .TitleHome2{
    @apply leading-[1.5] font-semibold text-base sm:text-xl
  }
  .pHome{
    @apply leading-[1.6] font-medium text-[14.5px]  sm:text-[16.5px]
  }
}